.appDownload {
  width: 100%;
  height: 40px;
  background: rgba(0, 0, 0, .55);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  .logo {
    width: 28px;
    height: 28px;
    display: block;
    margin: 6px;
    float: left;
    background: url('../../img/slice/logo.png') no-repeat;
    background-size: 100%;
  }
  .appDesc {
    line-height: 42px;
    font-size: 14px;
    color: #fff;
    float: left
  }
  .justDownload {
    font-size: 12px;
    padding: 6px 10px;
    background: #ffda00;
    color: #000;
    float: right;
    position: absolute;
    top: 50%;
    right: 25px;
    display: inline-block;
    text-decoration: none;
    border-radius: 5px;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%)
  }
  .justDownload:active {
    background: #ccae00
  }
  .closeModal {
    width: 15px;
    height: 15px;
    position: absolute;
    right: 3px;
    top: 5px;
    background: url('../../img/slice/downloadClose.png') no-repeat;
    background-size: 100%;
  }
}
.downloadAppEnter {
  animation: downloadAppEnter 1s 0s forwards;
}

@keyframes downloadAppEnter {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}
