	-webkit-align-items: center;
	align-items: center;
}

.weui_cell_ft {
	text-align: right;
	color: #888;
}

.weui_cell_primary {
	-webkit-box-flex: 1;
	-webkit-flex: 1;
	flex: 1;
}

.weui_cells_access .weui_cell:not(.no_access) {
	-webkit-tap-highlight-color: transparent;
}

.weui_cells_access .weui_cell:not(.no_access):active {
	background-color: #ECECEC;
}

.weui_cells_access a.weui_cell {
	color: inherit;
}

.weui_cells_access .weui_cell_ft:after {
	content: " ";
	display: inline-block;
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
	height: 6px;
	width: 6px;
	border-width: 2px 2px 0 0;
	border-color: #C8C8CD;
	border-style: solid;
	position: relative;
	top: -2px;
	top: -1px;
	margin-left: .3em;
}

.appDownload {
	width: 100%;
	height: 40px;
	background: rgba(0, 0, 0, 0.55);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
}

.appDownload .logo {
	width: 28px;
	height: 28px;
	display: block;
	margin: 6px;
	float: left;
	background: url("../img/groupCenter_sprite.png") no-repeat;
	background-position: -62px -62px;
	background-size: 100%;
}

.appDownload .appDesc {
	line-height: 42px;
	font-size: 14px;
	color: #fff;
	float: left;
}

.appDownload .justDownload {
	font-size: 12px;
	padding: 6px 10px;
	background: #ffda00;
	color: #000;
	float: right;
	position: absolute;
	top: 50%;
	right: 25px;
	display: inline-block;
	text-decoration: none;
	border-radius: 5px;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}

.appDownload .justDownload:active {
	background: #ccae00;
}

.appDownload .closeModal {
	width: 15px;
	height: 15px;
	position: absolute;
	right: 3px;
	top: 5px;
	background: url("../img/groupCenter_sprite.png") no-repeat;
	background-position: -124px -0px;
	background-size: 100%;
}

.downloadAppEnter {
	animation: downloadAppEnter 1s 0s forwards;
}

@keyframes downloadAppEnter {
	0% {
		transform: translateY(-100%);
	}

	100% {
		transform: translateY(0);
	}
}

.contentBody {
	width: 100%;
	height: 100%;
	position: absolute;
	overflow: hidden;
}

.pageHidden {
	width: 100%;
	position: absolute;
	left: 0;
	top: 0;
	visibility: hidden;
	transform: translateY(100%);
	opacity: 0;
}

.pageLoader {
	background: #fff;
}

.pageLoader .square-spin {
	width: 50%;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.pageLoader .square-spin > div {
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
	width: 50px;
	height: 50px;
	background: #80ddeb;
	-webkit-animation: square-spin 3s 0s cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
	animation: square-spin 3s 0s cubic-bezier(0.09, 0.57, 0.49, 0.9) infinite;
	margin: 0 auto;
}

.loaderLeave {
	animation: loaderLeave 1s 0s forwards;
}

.pageEnter {
	animation: pageEntery 1s 0s forwards;
}

@keyframes square-spin {
	25% {
		-webkit-transform: perspective(100px) rotateX(180deg) rotateY(0deg);
		transform: perspective(100px) rotateX(180deg) rotateY(0deg);
	}

	50% {
		-webkit-transform: perspective(100px) rotateX(180deg) rotateY(180deg);
		transform: perspective(100px) rotateX(180deg) rotateY(180deg);
	}

	75% {
		-webkit-transform: perspective(100px) rotateX(0deg) rotateY(180deg);
		transform: perspective(100px) rotateX(0deg) rotateY(180deg);
	}

	100% {
		-webkit-transform: perspective(100px) rotateX(0deg) rotateY(0deg);
		transform: perspective(100px) rotateX(0deg) rotateY(0deg);
	}
}

@keyframes loaderLeave {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
		z-index: -1;
		visibility: hidden;
	}
}

@keyframes pageEntery {
	0% {
		opacity: 0;
		transform: scale(0.95);
	}

	100% {
		opacity: 1;
		transform: scale(1);
	}
}

