@import "../vendor/normalize";
@import "../vendor/weui/modal";
@import "../vendor/weui/cell";
@import "../util/mixins";
@import "../component/downloadApp";
@import "../component/loading";

body {
  background: #efeff5;
}

a {
  text-decoration: none;
  color: inherit;
}

.top {
  position: relative;
  .banner {
    display: block;
  }
  .header {
    width: 74%;
    height: 30px;
    line-height: 30px;
    position: absolute;
    bottom: 0;
    left: 50%;
    z-index: 10;
    transform: translateX(-50%);
    display: flex;
    color: #eaffd1;
    font-size: 14px;
    border-radius: 10px;
    background-image: -moz-linear-gradient( 90deg, rgb(123,182,35) 0%, rgb(163,227,92) 100%);
    background-image: -webkit-linear-gradient( 90deg, rgb(123,182,35) 0%, rgb(163,227,92) 100%);
    background-image: -ms-linear-gradient( 90deg, rgb(123,182,35) 0%, rgb(163,227,92) 100%);
  }
  .title {
    flex: 1;
  }
  .activity {
    padding-left: 10px;
  }
  .service {
    text-align: right;
    padding-right: 10px;
  }
  .logoWrap {
    @media screen and (max-width: 320px) {
      width: 60px;
      height: 60px;
      .logo {
        width: 42px;
        height: 42px;
        top: 13px;
        left: 10px;
      }
    }
    @media screen and (min-width: 321px) and (max-width: 360px) {
      width: 70px;
      height: 70px;
      .logo {
        width: 49px;
        height: 49px;
        top: 15px;
        left: 11px;
      }
    }
    @media screen and (min-width: 361px) {
      width: 80px;
      height: 80px;
      .logo {
        width: 56px;
        height: 56px;
        top: 17px;
        left: 12px;
      }
    }
    background: url('../img/logoWrap.png') no-repeat;
    background-size: 100%;
    @include center();
  }
  .logo {
    background-size: 100%;
    border-radius: 50%;
    position: absolute;
  }
}

.card {
  padding-left: 10px;
  background: #fff;
  border-bottom: 12px solid #efeff5;
  .cardHeader, .cardBody {
    margin-top: 0;
    &:before {
      height: 0;
      width: 0;
    }
  }
  .cardHeader {
    .weui_cell {
      padding-left: 0;
      position: relative;
      top: 3px;
    }
    .weui_cell_bd {
      font-size: 13px;
      color: #9c9c9c
    }
    .weui_cell_ft {
      font-size: 12px;
      color: #c0c0c0;
    }
    .count {
      color: #2baa8f;
    }
  }
  .cardBody {
    &:after {
      width: 0;
      height: 0;
    }
    .weui_cell {
      padding: 5px 15px 5px 0;
      &:before {
        left: 0;
      }
    }
  }
}

.communityPhotos {
  .photos {
    padding: 10px 10px 10px 0;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }
  .photo {
    width: 19%;
    background-size: cover;
    background-repeat: no-repeat;
    display: block;
  }
  .img {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
  }
}

.communityPaoPao {
  .photos {
    padding: 10px 10px 10px 0;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }
  .photo {
    width: 30%;
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
    display: block;
  }
  .img {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
  }
  .label {
    width: 100%;
    font-size: 12px;
    background: #2baa8f;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    position: absolute;
    bottom: 0;
    @include ellipsis();
  }
}

.neighbor {
  .photos {
    padding: 10px 10px 10px 0;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }
  .photo {
    width: 18%;
    border-radius: 50%;
    background-size: cover;
    background-repeat: no-repeat;
    display: block;
  }
  .img {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
  }
}

.notices {
  .laba {
    width: 16px;
    height: 15px;
    background: url('community/laba.png') no-repeat;
    background-size: 100%;
    margin-right: 10px;
  }
  .notice {
    .weui_cell {
      font-size: 12px;
      color: #404040;
    }
  }
}

.communityTel {
  .cardBody {
    .weui_cell {
      font-size: 14px;
      color: #707070;
    }
  }
  .flag1, .flag2, .flag3 {
    width: 30px;
    height: 30px;
    margin-right: 10px;
  }
  .flag1 {
    background: url('../../img/slice/groupCenter/flag1.png') no-repeat;
  }
  .flag2 {
    background: url('../../img/slice/groupCenter/flag2.png') no-repeat;
  }
  .flag3 {
    background: url('../../img/slice/groupCenter/flag3.png') no-repeat;
  }
}

.communityService {
  .services {
    padding: 10px 10px 10px 0;
    display: flex;
    align-items: stretch;
    justify-content: space-between;
  }
  .service {
    width: 20%;
    background-size: cover;
    background-repeat: no-repeat;
    display: block;
  }
  .img {
    width: 100%;
    display: block;
  }
}
